
const ListingView = () => import('@/screen/Listing/View/ListingView')
const ListingPlus = () => import('@/screen/Listing/Plus/ListingPlus')
const ListingPlusReady = () => import('@/screen/Listing/PlusReady/ListingPlusReady')

export default [
  
  {
    path: 'listing/',
    component: ListingView,
    meta: {
      title: '报价'
    }
  },
  {
    path: 'listing_plus/',
    component: ListingPlus,
    meta: {
      title: '新增报价单'
    }
  },
  {
    path: 'listing_plus_ready/',
    component: ListingPlusReady,
    meta: {
      title: '新增报价单'
    }
  },
]


