import Router from 'vue-router'

import home from './home'

const HomeScreen = () => import('@/screen/Home/Home')
const User = () => import('@/screen/User/User')

export default new Router({
  mode: 'history',
  linkActiveClass: 'menu-active',
  routes: [
    {
      path: '',
      redirect: '/home',
      meta: {
        title: '首页'
      }
    },
    {
      path: '/home',
      component: HomeScreen,
      children: home
    },
    {
      path: '/user/:userId',
      component: User,
      meta: {
        title: '用户'
      }
    },
  ]
})



